.btn--light {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--light--color);
  border: 2px solid var(--accent--color);
  border-radius: 5px;
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: var(--accent--color);
    box-shadow: 0 0 30px rgba(255,255,255,0.6);
    color: var(--light--color);
  }
}

.btn--dark {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--dark--color);
  border: 2px solid var(--accent--color);
  border-radius: 5px;
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: var(--accent--color);
    box-shadow: 0 0 15px rgba(#268BE8,0.6);
    color: var(--light--color);
  }
}

.btn--dsbl {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #999999;
  border: 2px solid #CCCCCC;
  border-radius: 5px;
  background-color: transparent;
  transition: all 0.3s ease 0s;
  &:hover {
    border: 2px solid var(--accent--color);
  }
}

.title {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 70px;
  text-transform: uppercase;
  color: var(--light--color);
  @media (max-width: 992px){
      font-size: 50px;
      line-height: 60px;
  }
  @media (max-width: 768px){
      font-size: 38px;
      line-height: 50px;
  }
  @media (max-width: 576px){
      font-size: 32px;
      line-height: 42px;
  }
}
