@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

.all_pages{
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover{
    ul{
      right: 0;
    }
  }
  .page_point{
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul{
    background: #00D2FF;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: .4s;
    max-height: 450px;
    overflow-y: auto;
    li{
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a{
        color: #fff;
      }
    }
  }
}


// HEADER START ========================================================================================================================================================

body {
  &.lock {
    overflow: hidden;
  }
}

.header-profile {
  .header {
    background-color: #03111D;
  }
  .header__registration {
    display: none;
  }
  .header__avatar {
    display: block;
  }
}
.header {
  padding: 40px 0;
  position: relative;
  transition: all 0.3s ease 0s;
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  @media (max-width: 992px){
    padding: 20px 0;
  }
  // .header__wrap

  &__wrap {
    display: flex;
    align-items: center;
    @media (max-width: 992px){
      position: relative;
      .header__registration {
        position: absolute;
        left: 50%;
        transform: translateX(-50%) scale(1);
        margin-left: 0;
        bottom: 10%;
        z-index: 102;
      }
    }
    @media (max-height: 480px){
      .header__registration {
        padding: 2px 20px;
        font-size: 12px;
        left: auto;
        right: -5%;
        bottom: 3%;
      }
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 101;
    margin-right: auto;
    img {
      transition: all 0.3s ease 0s;
    }
    @media (max-width: 768px){
      img {
        max-width: 70%;
      }
    }
  }

  // .header__registration

  &__registration {
    margin-left: 60px;
    @media (max-width: 1200px){
      margin-left: 40px;
    }
    @media (max-width: 992px){
      transform: scale(0);
    }
  }

  &__avatar {
    margin-left: 60px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: none;
    transition: all 0.3s ease 0s;
    @media (max-width: 992px){
        margin-right: 30px;
        width: 40px;
        height: 40px;
    }
    img {
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.3s ease 0s;
    }
    @media (max-width: 1200px){
      margin-left: 40px;
    }
  }

  .nav {
    > ul {
      display: flex;
      align-items: center;
      padding-left: 0;
      margin-bottom: 0 !important;
      padding-left: 0 !important;
      > li {
        &:nth-child(4) {
          font-family: 'Kanit';
          font-style: normal;
          font-size: 16px !important;
          font-weight: 500;
          line-height: 24px;
          text-transform: uppercase;
          color: var(--light--color);
          transition: all 0.3s ease 0s;
          position: relative;
          cursor: pointer;
          padding-right: 15px;
          @media (max-width: 992px){
              padding-right: 0;
              font-size: 20px !important;
          }
          @media (max-width: 480px){
              text-align: center;
          }
          &:after {
            content: '';
            position: absolute;
            right: 0px;
            top: 7px;
            transform-origin: 50% 75%;
            transform: rotate(45deg);
            width: 7px;
            height: 7px;
            background: 2px solid var(--light--color);
            border: 2px solid var(--light--color);
            border-left: none;
            border-top: none;

            transition: all 0.3s ease 0s;
            @media (max-width: 1200px){
                width: 6px;
                height: 6px;
            }
            @media (max-width: 992px){
                width: 8px;
                height: 8px;
                right: -15px;
                top: 10px;
                transition-duration: 0s;
            }
            @media (max-width: 480px){
              width: 6px;
              height: 6px;
              top: 8px;
              right: -10px;
          }
          }
          &.active {
            &:after {
              @media (max-width: 480px){
                right: -5px;
              }
            }
          }
          > ul {
            position: absolute;
            top: calc(100% + 10px);
            background-color: rgba(0,0,0,0.7);
            padding: 15px;
            border-radius: 10px;
            width: 140%;
            visibility: hidden;
            opacity: 0;
            z-index: -1;
            transition: all 0.3s ease 0s;
            @media (max-width: 992px){
                position: static;
                width: 100%;
                text-align: center;
                display: inline;
                padding: 0;
                display: none;
            }
            > li {
              margin-bottom: 5px;
              &:last-child {
                margin-bottom: 0;
              }
              > a {
                display: inline-block;
                font-family: 'Kanit';
                font-style: normal;
                font-size: 14px;
                font-weight: 500;
                line-height: 24px;
                text-transform: uppercase;
                color: var(--light--color);
                position: relative;
                transition: all 0.3s ease 0s;
                &:hover {
                  color: var(--accent--color);
                  @media (max-width: 992px){
                    color: var(--light--color);
                  }
                }
                @media (max-width: 1200px){
                    font-size: 12px;
                }
                @media (max-width: 480px){
                    font-size: 12px;
                }
              }
            }
          }
          &.active {
            > ul {
              visibility: visible;
              opacity: 1;
              z-index: 10;
              @media (max-width: 992px){
                display: block;
                > li {
                  margin-bottom: 0;
                  line-height: 110%;
                }
              }
            }
            &:after {
              transform: rotate(225deg);
            }
          }
          @media (max-width: 1200px){
              font-size: 14px !important;
              line-height: 25px !important;
          }
          @media (max-width: 992px){
            font-size: 20px !important;
            line-height: 32px !important;
            @media (max-width: 480px){
              font-size: 16px !important;
              line-height: 26px !important;
            }
          }
        }
        margin-right: 30px;
        &:last-child {
          margin-right: 0;
        }
        > a {
          display: inline-block;
          font-family: 'Kanit';
          font-style: normal;
          font-weight: 500;
          line-height: 24px;
          text-transform: uppercase;
          color: var(--light--color);
          position: relative;
          transition: all 0.3s ease 0s;
          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -5px;
            height: 3px;
            width: 0%;
            background-color: var(--accent--color);
            transition: all 0.3s ease 0s;
          }
          &:hover:after {
            width: 100%;
            @media (max-width: 992px){
              width: 0%;
            }
          }
          &.active {
            &:after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -5px;
              height: 3px;
              width: 100%;
              background-color: var(--accent--color);
              transition: all 0.3s ease 0s;
              @media (max-width: 992px){
                width: 0%;
              }
            }
          }
        }
      }
    }
    // .nav__list

    &__list {}

    // .nav__item

    &__item {}

    // .nav__link

    &__link {}
    @media (max-width: 992px){
        transform: translateX(100%);
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 100;
        //overflow-y: auto;
        width: 100%;
        height: 100vh;
        background-color: #000000;
        transition: all 0.3s ease 0s;
        > ul {
          width: 100%;
          height: 100%;
          flex-direction: column;
          justify-content: center;
          > li {
            margin-right: 0 !important;
            > a {
              font-size: 20px !important;
              line-height: 32px !important;
              @media (max-width: 480px){
                font-size: 16px !important;
                line-height: 26px !important;
              }
          }
        }
      }
      &.menu--active {
        transform: translateX(0%);
      }
    }
    @media (max-height: 280px){
        > ul {
          > li {
            > a {
              font-size: 12px !important;
              line-height: 24px !important;
            }
          }
        }
    }
  }
  .burger {

    @include burger;
    display: none;
    z-index: 101;
    @media (max-width: 992px){
      display: block;
    }

    // .burger__line

    &__line {}
  }

  // .header__btn

  &__btn {
    font-size: 16px;
    padding: 8px 53px;
    @media (max-width: 1200px){
      font-size: 14px;
      padding: 6px 40px;
    }
    @media (max-height: 280px){
      padding: 4px 30px !important;
      font-size: 12px !important;
    }
  }
  &.scroll {
    padding: 10px 0;
    background-color: rgba(0,0,0,0.8);
    nav {
      > ul {
        > li {
          &:nth-child(4) {
            line-height: 25px;
            > ul {
              > li {
                > a {
                  font-size: 12px;
                }
              }
            }
          }
          > a {
            font-size: 14px;
            &:after {
              bottom: -1px;
              height: 2px;
            }
            &.active {
              &:after {
                bottom: -1px;
                height: 2px;
              }
            }
          }
        }
      }
    }
    a.header__btn {
      padding: 6px 40px;
      font-size: 14px;
    }
    a.header__logo {
      img {
        max-width: 70%;
      }
    }
    .header__avatar, .header__avatar_dashboard {
      width: 40px;
      height: 40px;
    }
  }
  @media (max-width: 1200px){
    .nav {
      > ul {
        > li {
          margin-right: 20px;
          > a {
            font-size: 14px;
          }
        }
      }
    }
  }
  .header__avatar_dashboard {
    margin-left: 60px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    transition: all 0.3s ease 0s;
    @media (max-width: 992px){
        margin-right: 30px;
        width: 40px;
        height: 40px;
    }
    img {
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.3s ease 0s;
    }
    @media (max-width: 1200px){
      margin-left: 40px;
    }
  }
}

// HEADER END ========================================================================================================================================================

// PROMO SECTION START ========================================================================================================================================================

.promo {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 370px;
  padding-bottom: 390px;
  @media (max-width: 1200px){
    padding-top: 300px;
    padding-bottom: 300px;
  }
  @media (max-width: 992px){
      padding-top: 250px;
      padding-bottom: 250px;
  }
  @media (max-width: 480px){
      padding-top: 180px;
      padding-bottom: 180px;
  }
  // .promo__wrap

  &__wrap {
    padding-left: 80px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0px;
      width: 10px;
      height: calc(100% - 10px);
      background-color: var(--accent--color);
    }
    @media (max-width: 768px){
      padding-left: 50px;
    }
    @media (max-width: 576px){
      padding-left: 30px;
      &:after {
        width: 6px;
      }
    }
  }

  // .promo__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 70px;
    text-transform: uppercase;
    color: var(--light--color);
    max-width: 992px;
    @media (max-width: 1200px){
        font-size: 50px;
        line-height: 60px;
    }
    @media (max-width: 992px){
        font-size: 40px;
        line-height: 50px;
    }
    @media (max-width: 768px){
        font-size: 35px;
    }
    @media (max-width: 576px){
      font-size: 32px;
      line-height: 45px;
      font-weight: 500;
    }
    @media (max-width: 480px){
      font-size: 25px;
      line-height: 35px;
    }
  }

  // .promo__btn

  &__btn {
    display: inline-block;
    margin-top: 60px;
    padding: 13px 75px;
    @media (max-width: 768px){
        margin-top: 40px;
    }
    @media (max-width: 576px){
        padding: 10px 45px;
        font-size: 14px;
    }
  }
}

// PROMO SECTION END ========================================================================================================================================================

// COURSES SECTION START ========================================================================================================================================================

.courses {
  padding-top: 120px;
  @media (max-width: 768px){
    padding-top: 50px;
  }
  // .courses__wrap

  &__wrap {}

  // .courses__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    text-transform: uppercase;
    color: var(--dark--color);
    @media (max-width: 768px){
        font-size: 40px;
        line-height: 50px;
    }
    @media (max-width: 480px){
        font-size: 32px;
        line-height: 37px;
    }
  }

  // .courses__descr

  &__descr {
    margin-top: 20px;
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    color: var(--dark--color);
    @media (max-width: 480px){
        font-size: 14px;
        line-height: 26px;
    }
  }

  // .courses__btn-all

  &__btn-all {
    background-color: transparent;
    outline: none;
    cursor: pointer;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: var(--dark--color);
    padding: 10px 50px;
    margin: 0 auto;
    display: block;
    @media (max-width: 480px){
        font-size: 12px;
        padding: 8px 30px;
    }
  }

  .cards {
    list-style: none;
    margin-top: 60px;
    margin-bottom: 60px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-auto-rows: auto;
    column-gap: 30px;
    overflow-y: hidden;
    padding-left: 0 !important;
    @media (max-width: 1400px){
      column-gap: 15px;
    }
    @media (max-width: 1200px){
      justify-items: center;
      row-gap: 50px;
    }
    @media (max-width: 768px){
      grid-template-columns: repeat(8, 1fr);
    }
    @media (max-width: 480px){
      grid-template-columns: repeat(8, 1fr);
      margin-top: 35px;
      margin-bottom: 50px;
      row-gap: 35px;
    }
    // .cards__item

    &__item {
      grid-column: span 3;
      @media (max-width: 1200px){
        grid-column: span 4;
        max-width: 315px;
      }
      @media (max-width: 768px){
        grid-column: span 4;
      }
      @media (max-width: 480px){
        grid-column: span 8;
      }
      &:hover {
        cursor: pointer;
        .card-item__name {
          text-shadow: 1px 1px rgba(0,0,0,0.5);
        }
        .card-item__label-price {
          color: #09559b;
        }
        .card-item__image {
          img {
            filter: blur(2px);
          }
        }
      }
    }
  }
  .card-item {
    height: 100%;
    display: flex;
    flex-direction: column;

    // .card-item__image

    &__image {
      height: 230px;
      position: relative;
      margin-bottom: 10px;
      img {
        height: 100%;
        object-fit: cover;
        transition: all 0.3s ease 0s;
      }
    }

    // .card-item__label-price

    &__label-price {
      position: absolute;
      right: 15px;
      bottom: 15px;
      font-family: 'Kanit';
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 30px;
      color: var(--accent--color);
      width: 80px;
      height: 30px;
      padding-left: 30px;
      background-position: left top;
      background-repeat: no-repeat;
      background-size: cover;
      transition: all 0.3s ease 0s;
    }

    // .card-item__content

    &__content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex-grow: 1;
    }

    // .card-item__name

    &__name {
      font-family: 'Kanit';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 30px;
      color: var(--dark--color);
      margin-bottom: 5px;
      transition: all 0.3s ease 0s;
    }

    // .card-item__indiv

    &__indiv {
      font-family: 'Kanit';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      color: var(--light--color);
      font-variant: small-caps;
      background-color: #0D4579;
      border-radius: 2px;
      padding: 2px 10px;
      margin-bottom: 10px;
    }

    // .card-item__text

    &__text {
      padding: 0;
      margin: 0;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--dark--color);
      margin-bottom: 20px;
    }

    // .card-item__btn

    &__btn {
      margin-top: auto;
      padding: 8px 49px;
      @media (max-width: 768px){
          width: 100%;
          text-align: center;
      }
      @media (max-width: 480px){
        margin-left: auto;
        margin-right: auto;
        padding: 6px 35px;
        font-size: 14px;
      }
    }
  }
}


// COURSES SECTION END ========================================================================================================================================================

