// SECURITY SECTION START ========================================================================================================================================================

.security {
  background-color: #F9F9F9;
  padding-top: 150px;
  padding-bottom: 150px;
  @media (max-width: 1200px){
      padding-top: 75px;
      padding-bottom: 75px;
  }
  // .security__wrap

  &__wrap {
    display: flex;
    column-gap: 136px;
    @media (max-width: 1200px){
        column-gap: 50px;
    }
    @media (max-width: 1200px){
        flex-direction: column;
    }
  }

  // .security__image

  &__image {
    flex-basis: 400px;
    margin-left: 40px;
    position: relative;
    max-height: 500px;
    img {
      height: 100%;
    }
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: -40px;
      width: 40px;
      height: 80%;
      background-color: var(--accent--color);
    }
    @media (max-width: 1200px){
        flex-basis: 100%;
        max-width: 440px;
        margin: 0 auto;
        margin-bottom: 40px;
    }
    @media (max-width: 768px){
        max-width: 60%;
        &:after {
          content: none;
        }
    }
    @media (max-width: 480px){
        max-width: 80%;
    }
  }

  // .security__content

  &__content {
    flex-basis: calc(100% - 576px);
    @media (max-width: 1200px){
      flex-basis: 100%;
      padding: 50px 150px;
      border-color: var(--light--color);
      box-shadow: 0 0 25px 0 rgba(0,0,0,0.1);
    }
    @media (max-width: 992px){
       padding: 50px 100px;
    }
    @media (max-width: 768px){
        padding: 50px 20px;
    }
    @media (max-width: 480px){
      padding: 0;
      border-color: none;
      box-shadow: none;
    }
  }

  // .security__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
    text-transform: uppercase;
    color: var(--dark--color);
    @media (max-width: 768px){
        font-size: 30px;
        line-height: 36px;
    }
    @media (max-width: 480px){
      font-size: 26px;
      line-height: 32px;
    }
  }

  // .security__name

  &__name {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: var(--dark--color);
    margin-top: 30px;
    @media (max-width: 480px){
      font-size: 22px;
      margin-top: 20px;
    }
  }

  // .security__prof

  &__prof {
    margin-top: 5px;
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--accent--color);
    @media (max-width: 480px){
      font-size: 14px;
      line-height: 25px;
    }
  }

  // .security__text

  &__text {
    margin-top: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    color: var(--dark--color);
  }

  // .security__btn

  &__btn {
    margin-top: 60px;
    display: inline-block;
    padding: 8px 52px;
    @media (max-width: 1200px){
        margin-top: 30px;
    }
    //margin-top: 20px;
    @media (max-width: 480px){
      margin-left: auto;
      margin-right: auto;
      padding: 6px 35px;
      font-size: 14px;
    }
  }
}

// SECURITY SECTION END ========================================================================================================================================================


// TESTIMONIALS SECTION START ========================================================================================================================================================

.testimonials {
  padding-top: 150px;
  padding-bottom: 150px;
  @media (max-width: 1200px){
    padding-top: 75px;
    padding-bottom: 75px;
  }
  // .testimonials__wrap

  &__wrap {}

  // .testimonials__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
    text-transform: uppercase;
    color: var(--dark--color);
    text-align: center;
    @media (max-width: 768px){
        font-size: 30px;
        line-height: 36px;
    }
    @media (max-width: 480px){
      font-size: 26px;
      line-height: 32px;
    }
  }

  // .testimonials__slider

  &__slider {
    margin-top: 60px;
    display: flex;
    @media (max-width: 480px){
        margin-top: 40px;
    }
  }

  // .testimonials__slide

  &__slide {
    padding-left: 45px;
    padding-right: 30px;
    box-sizing: border-box;
    width: calc(1350px / 3);
    display: flex;
    align-items: stretch;
    flex-direction: column;
    border-right: 1px solid #DDDDDD;
    @media (max-width: 992px){
      padding-right: 25px;
      padding-left: 25px;
    }
    @media (max-width: 576px){
        padding: 15px;
        border: 1px solid rgba(#dddddd, 0.2);
        box-shadow: inset 0 0 25px rgba(0,0,0,0.1);
        border-radius: 5px;
        background-color: #ffffff;
    }
    @media (max-width: 480px){
        padding-right: 10px;
        padding-left: 10px;
    }
  }

  // .testimonials__image

  &__image {
    @media (max-width: 480px){
        img {
          width: 35px;
          height: 35px;
        }
    }
  }

  // .testimonials__content

  &__content {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    justify-content: center;
    flex-direction: column;
    flex-grow: 1;
    @media (max-width: 480px){
        margin-top: 20px;
    }
  }

  // .testimonials__text

  &__text {
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    color: var(--dark--color);
    margin-bottom: 45px;
    @media (max-width: 480px){
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 25px;
    }
  }

  // .testimonials__date

  &__date {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    color: var(--accent--color);
    margin-top: auto;
    @media (max-width: 480px){
        font-size: 15px;
        line-height: 23px;
    }
  }

  .swiper {
    padding-bottom: 60px;
  }
  .swiper-slide-next {
    border-right: none;
  }
  .swiper-slide-prev {
    padding-left: 0 !important;
  }
  .swiper-testimonials {

  }
  .swiper-wrapper {
  }
  .swiper-slide {
  }
  .swiper-pagination {
  }
  .swiper-pagination-bullet {
    width: 30px;
    height: 5px;
    background-color: #DDDDDD;
    border-radius: 2px;
  }
  .swiper-pagination-bullet-active {
    width: 60px;
    height: 5px;
    background-color: var(--accent--color);
    border-radius: 2px;
  }
}

// TESTIMONIALS SECTION END ========================================================================================================================================================

// SERVICES SECTION START ========================================================================================================================================================

.services {
  padding-top: 110px;
  padding-bottom: 200px;
  overflow-y: hidden;
  @media (max-width: 1200px){
    padding-top: 75px;
    padding-bottom: 125px;
  }
  // .services__wrap

  &__wrap {}

  // .services__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
    text-transform: uppercase;
    color: var(--dark--color);
    text-align: center;
    @media (max-width: 768px){
        font-size: 30px;
        line-height: 36px;
    }
    @media (max-width: 480px){
      font-size: 26px;
      line-height: 32px;
    }
  }

  .cards {
    margin-top: 60px;
    display: flex;
    column-gap: 30px;
    padding-left: 0 !important;
    @media (max-width: 1200px){
        flex-wrap: wrap;
        justify-content: center;
        row-gap: 30px;
    }
    @media (max-width: 768px){
      column-gap: 15px;
      margin-top: 40px;
    }

    // .cards__item

    &__item {
      background-color: var(--light--color);
      padding: 30px 30px 40px 30px;
      flex: 0 1 25%;
      transition: all 0.3s ease 0s;
      cursor: pointer;
      &:hover {
        border-color: var(--light--color);
        box-shadow: 0 0 25px 0 rgba(0,0,0,0.5);
        transform: translateY(-10px);
        @media (max-width: 992px){
          border-color: var(--light--color);
          box-shadow: 0 0 15px 0 rgba(0,0,0,0.2);
          transform: translateY(0px);
        }
      }
      @media (max-width: 1200px){
         flex: 0 1 calc(100% / 3);
         max-width: 315px;
      }
      @media (max-width: 992px){
        flex: 0 1 calc(100% / 2);
        border-color: var(--light--color);
        box-shadow: 0 0 15px 0 rgba(0,0,0,0.2);
      }
      @media (max-width: 768px){
        padding: 30px 20px;
        max-width: 45%;
      }
      @media (max-width: 576px){
        flex: 0 1 100%;
        max-width: 100%;
      }
    }
  }
  .card-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    transition: all 0.3s ease 0s;

    // .card-item__name

    &__name {
      font-family: 'Kanit';
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 25px;
      text-transform: uppercase;
      color: var(--dark--color);
      max-width: 70%;
      min-height: 50px;
      @media (max-width: 1400px){
          max-width: 100%;
      }
    }

    // .card-item__text

    &__text {
      padding: 0;
      margin: 0;
      flex-grow: 1;
      margin-top: 10px;
      margin-bottom: 30px;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 25px;
      color: var(--dark--color);
    }

    // .card-item__btn

    &__btn {
      margin-top: auto;
      padding: 8px 48px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}


// SERVICES SECTION END ========================================================================================================================================================


// FOOTER STARTS ========================================================================================================================================================

.footer {
  padding-top: 140px;
  @media (max-width: 992px){

  }
  .container {
    position: relative;
  }
  // .footer__wrap

  &__wrap {

  }
  // .logo-footer

  .logo__footer {
    //margin-right: auto;
    @media (max-width: 992px){
        display: inline-block;
        padding-top: 30px;
    }
  }
  // .footer__navigation

  &__navigation {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-bottom: 60px;
    border-bottom: 1px solid #0D4579;
    > nav {
      flex-grow: 1;
      text-align: right;
      > ul {
        padding-left: 0;
        display: flex;
        justify-content: flex-end;
        > li {
          &:nth-child(4) {
            font-family: 'Kanit';
            font-style: normal;
            font-size: 16px !important;
            font-weight: 500;
            line-height: 24px;
            text-transform: uppercase;
            color: var(--light--color);
            transition: all 0.3s ease 0s;
            position: relative;
            cursor: pointer;
            padding-right: 15px;
            @media (max-width: 992px){
                padding-right: 0;
                font-size: 20px !important;
            }
            @media (max-width: 480px){
                text-align: center;
            }
            &:after {
              content: '';
              position: absolute;
              right: 0px;
              top: 7px;
              transform-origin: 50% 75%;
              transform: rotate(45deg);
              width: 7px;
              height: 7px;
              background: 2px solid var(--light--color);
              border: 2px solid var(--light--color);
              border-left: none;
              border-top: none;

              transition: all 0.3s ease 0s;
              @media (max-width: 1200px){
                  width: 6px;
                  height: 6px;
              }
              @media (max-width: 992px){
                  width: 6px;
                  height: 6px;
                  right: -10px;
                  top: 14px;
                  transition-duration: 0s;
              }
              @media (max-width: 480px){
                width: 6px;
                height: 6px;
                top: 8px;
                right: -10px;
            }
            }
            &.active {
              &:after {
                @media (max-width: 992px){
                    top: 14px;
                    right: 5px;
                }
                @media (max-width: 480px){
                  right: 5px;
                  top: 10px;
                }
              }
            }
            > ul {
              position: absolute;
              bottom: calc(100% + 10px);
              background-color: rgba(0,0,0,0.7);
              padding: 15px;
              border-radius: 10px;
              width: 140%;
              visibility: hidden;
              opacity: 0;
              z-index: -1;
              text-align: left;
              transition: all 0.3s ease 0s;
              @media (max-width: 992px){
                  position: static;
                  width: 100%;
                  text-align: center;
                  padding: 0;
                  display: none;
              }
              > li {
                margin-bottom: 5px;
                &:last-child {
                  margin-bottom: 0;
                }
                > a {
                  display: inline-block;
                  font-family: 'Kanit';
                  font-style: normal;
                  font-size: 14px;
                  font-weight: 500;
                  line-height: 24px;
                  text-transform: uppercase;
                  color: var(--light--color);
                  position: relative;
                  transition: all 0.3s ease 0s;
                  &:hover {
                    color: var(--accent--color);
                    @media (max-width: 992px){
                      color: var(--light--color);
                    }
                  }
                  @media (max-width: 1200px){
                      font-size: 12px;
                  }
                  @media (max-width: 480px){
                      font-size: 12px;
                  }
                }
              }
            }
            &.active {
              > ul {
                visibility: visible;
                opacity: 1;
                z-index: 10;

                @media (max-width: 992px){
                  display: block;
                  background-color: transparent !important;
                  > li {
                    margin-bottom: 0;
                    line-height: 110%;
                  }
                }
              }
              &:after {
                transform: rotate(225deg);
              }
            }
            @media (max-width: 1200px){
                font-size: 14px !important;
                line-height: 25px !important;
            }
            @media (max-width: 992px){
              font-size: 20px !important;
              line-height: 32px !important;
              @media (max-width: 480px){
                font-size: 16px !important;
                line-height: 26px !important;
              }
            }
          }
          margin-right: 30px;
          &:last-child {
            margin-right: 0;
          }
          @media (max-width: 1200px){
              margin-right: 20px;
          }
          > a {
            font-family: 'Kanit';
            font-style: normal;
            font-weight: 500;
            line-height: 24px;
            text-transform: uppercase;
            color: var(--light--color);
            transition: all 0.3s ease 0s;
            &:hover {
              text-decoration: underline !important;
              @media (max-width: 992px){
                text-decoration: none !important;
              }
              //color: var(--accent--color);
              @media (max-width: 992px){
                  color: var(--light--color);
              }
            }
            @media (max-width: 1200px){
                font-size: 14px;
            }
          }
          @media (max-width: 992px){
              margin-right: 0;
              margin-bottom: 7px;
          }
        }
        @media (max-width: 992px){
            flex-direction: column;
            justify-content: center !important;
        }
      }
      @media (max-width: 992px){
        text-align: center;
      }
    }
    > ul {
      padding-left: 0;
      margin-left: 60px;
      display: flex;
      > li {
        margin-right: 10px;
        &:last-child {
          margin-right: 0;
        }
        > a {
          width: 30px;
          height: 30px;
          display: inline-block;
          border-radius: 50%;
          transition: all 0.3s ease 0s;
          &:hover {
            box-shadow: 0px 0px 15px rgba(255,255,255,0.5);
            transform: translateY(-3px);
          }
        }
      }
      @media (max-width: 992px){
          margin-left: 0;
      }
    }
    @media (max-width: 992px){
      flex-direction: column;
      row-gap: 30px;
    }
  }

  // .footer__list

  &__list {}

  // .footer__item

  &__item {}

  // .footer__link

  &__link {}

  // .footer__copyright

  &__copyright {
    padding-top: 24px;
    padding-bottom: 26px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 768px){
        flex-direction: column;
    }
  }

  // .footer__design

  &__design {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #AAAAAA;
    a {
      color: var(--light--color) !important;
      &:hover {
        color: var(--light--color) !important;
        text-decoration: underline !important;
      }
    }
    @media (max-width: 768px){
        margin-bottom: 7px;
        font-size: 12px;
        text-align: center;
    }
  }

  // .footer__policy

  &__policy {
    a {
      display: inline-block;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light--color) !important;
      &:last-of-type {
        margin-left: 10px;
      }
      &:hover {
        color: var(--light--color) !important;
        text-decoration: underline !important;
      }
      @media (max-width: 768px){
          font-size: 12px;
      }
    }
  }

  .social {

    // .social__item

    &__item {}

    // .social__link

    &__link {}

    // .social__link--fb

    &__link--fb {}

    // .social__link--vk

    &__link--vk {}

    // .social__link--tw

    &__link--tw {}
  }

  .subscribe {
    max-width: 1350px;
    padding: 40px 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: left center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    width: calc(100% + 1px);
    left: 50%;
    transform: translateX(-50%);
    top: -220px;
    @media (max-width: 1200px){
        padding: 60px 20px;
    }
    @media (max-width: 992px){
        flex-direction: column;
        text-align: center;
        padding: 30px 20px;
    }
    // .subscribe__title

    &__title {
      font-family: 'Kanit';
      font-style: normal;
      font-weight: 700;
      font-size: 30px;
      line-height: 40px;
      text-transform: uppercase;
      color: var(--light--color);
      max-width: 480px;
      @media (max-width: 1200px){
        font-size: 25px;
        line-height: 35px;
      }
      @media (max-width: 992px){
          font-size: 20px;
          line-height: 28px;
      }
    }

    // .subscribe__form

    &__form {
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
      @media (max-width: 992px){
          justify-content: center;
      }
      form {
        position: relative;
        width: 460px;
        @media (max-width: 480px){
            width: 100%;
        }
      }
      label {
        width: 100%;
      }
      input {
        width: 100%;
        background-color: transparent;
        border: none;
        outline: none;
        padding: 10px 30px 10px 0;
        border-bottom: 1px solid rgba(#ffffff, 0.3);
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 30px;
        color: var(--light--color);
        border-radius: 0 !important;
        &::placeholder {
          font-style: normal;
          font-weight: 400;
          line-height: 30px;
          color: rgba(255,255,255,0.5);
          @media (max-width: 480px){
            font-size: 14px;
            line-height: 20px;
         }
        }
        @media (max-width: 992px){
            margin-top: 20px;
        }
        @media (max-width: 480px){
          margin-top: 10px;
          font-size: 14px;
          line-height: 20px;
          padding: 10px 20px 10px 0;
      }
      }
      @media (max-width: 992px){
          flex-grow: 0;
          width: 100%;
          text-align: center;
      }
    }

    // .subscribe__input

    &__input {}

    // .subscribe__btn

    &__btn {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      transform-origin: left top;
      transition: all 0.3s ease 0s;
      @media (max-width: 480px){
          width: 18px;
          height: 18px;
      }
      &:hover {
        transform: translate(5px, -12px);
      }
    }
  }
}

.footer-login {
  .footer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: transparent !important;
    padding-top: 0;
    .footer__navigation, .subscribe {
      display: none;
    }
    .footer__copyright {
      border-top: 1px solid #0D4579
    }
  }
}
