// HERO SECTIONS START ========================================================================================================================================================

.hero {
    padding-top: 240px;
    padding-bottom: 160px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-height: 500px;
    @media (max-width: 992px){
        padding-top: 180px;
    }
		// .hero__wrap

		&__wrap {
      padding-left: 80px;
      position: relative;
      nav {
        margin-top: 10px;
        > ol {
          color: var(--light--color) !important;
          > li {
            display: flex;
            align-items: center;
            &::before {
              color: var(--light--color) !important;
            }
            > a {
              padding-top: 0;
              transform: translateY(-1px);
              display: inline-block;
              font-family: 'Kanit';
              font-style: normal;
              font-weight: 400;
              font-size: 15px;
              line-height: 15px;
              text-transform: uppercase;
              color: var(--light--color);
              transition: all 0.3s ease 0s;
              &:hover {
                color: var(--accent--color);
              }
              @media (max-width: 576px){
                  font-size: 12px;
              }
            }
            &.active {
              font-family: 'Kanit';
              font-style: normal;
              font-weight: 400;
              font-size: 15px;
              line-height: 24px;
              text-transform: uppercase;
              color: var(--light--color);
              &::before {
                color: var(--light--color);
              }
              @media (max-width: 576px){
                  font-size: 12px;
              }
            }
