  // tooltips

  /* Tooltip container */
  .tooltips {
    margin-left: 10px;
    transform: translateY(-6px);
    display: inline-block;
    cursor: pointer;
  }

  /* Tooltip text */
  .tooltips .tooltiptext {
    visibility: hidden;
    width: 220px;
    background-color: #ffffff;
    color: var(--dark--color);
    text-align: center;
    padding: 15px 10px;
    filter: drop-shadow(0px 5px 20px rgba(0, 0, 0, 0.4));
    @media (max-width: 576px) {
      width: 180px;
      font-size: 14px;
      padding: 10px;
    }

    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 0%;
    margin-left: -140px;


    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }

  /* Tooltip arrow */
  .tooltips .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 148px;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
    @media (max-width: 576px) {
      left: 148px;
    }
  }

  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltips:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }

