// SECTION CHECKOUT START ========================================================================================================================================================

.checkout {
  padding-top: 190px;
  padding-bottom: 230px;
  @media (max-width: 768px){
      padding-top: 140px;
      padding-bottom: 150px;
  }
  // .checkout__wrap

  &__wrap {
    display: flex;
    column-gap: 110px;
    justify-content: space-between;
    @media (max-width: 1400px){
        column-gap: 50px;
    }
    @media (max-width: 1200px){
      column-gap: 20px;
    }
    @media (max-width: 768px){
      flex-direction: column;
    }
  }

  // .checkout__pay

  &__pay {
    flex-basis: 890px;
    @media (max-width: 1400px){
      flex-basis: 65%;
    }
    @media (max-width: 992px){
        flex-basis: 58%;
    }
    @media (max-width: 768px){
      flex-basis: 100%;
    }
  }

  // .checkout__title

  &__title {
    font-family: 'Kanit';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 40px;
    text-transform: uppercase;
    color: var(--dark--color);
  }

  // .checkout__form

  &__form {
    margin-top: 30px;
    input, label {
      width: 100%;
    }
    input {
      outline: none;
      border: none;
      font-family: 'OpenSans';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 25px;
      padding: 5px 0;
      border-bottom: 1px solid rgba(#cccccc, 0.3);
      margin-bottom: 30px;
      border-radius: 0 !important;
      &::placeholder {
        color: #cccccc;
      }
    }
    form {
      > .checkout__label-div {
        position: relative;
        width: 100%;
        display: flex;
        column-gap: 20px;
        margin-bottom: 10px;
        border: 1px solid rgba(#cccccc, 0.3);
        padding: 20px 30px;
        cursor: pointer;
        &:last-child {
          margin-bottom: 0;
        }
        .card-images {
          position: absolute;
          top: 20px;
          right: 30px;
          @media (max-width: 576px){
              top: 10px;
              right: 10px;
              width: 70px;
          }
        }
        > span {

          width: 30px;
          height: 30px;
          //margin-right: 16px;
          position: relative;
          @media (max-width: 480px){
              width: 24px;
              height: 24px;
          }
          &:before {
            content: "";
            width: 30px;
            height: 30px;
            border: 1px solid #dddddd;
            display: inline-block;
            border-radius: 50%;
            transition: all 0.3s ease 0s;
            @media (max-width: 480px){
              width: 24px;
              height: 24px;
            }

          }
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            width: 16px;
            height: 16px;
            background-color: var(--accent--color);
            opacity: 0;
            transition: opacity 0.3s cubic-bezier(.66,.08,.83,.67);
            @media (max-width: 480px){
                width: 12px;
                height: 12px;
            }
          }
        }
        input[type="radio"] {
          position: absolute;
          width: 1px;
          height: 1px;
          opacity: 0;
        }
        input[type="radio"]:checked + span:after {
          opacity: 1;
          transition: opacity 0.3s cubic-bezier(.11,.83,.46,.97) 0.1s;
        }
        input[type="radio"]:checked + span:before {
          border: 1px solid var(--accent--color);
        }
        @media (max-width: 1200px){
            padding: 20px 15px;
        }
        @media (max-width: 480px){
            column-gap: 5px;
        }
      }
      span {
        font-family: 'OpenSans';
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        text-transform: uppercase;
        color: var(--dark--color);
      }
    }
  }

  .tooltip__wrap {
    span {
      //margin-left: 10px;
      //transform: translateY(-6px);
      cursor: pointer;
    }
    //display: flex;
  }

  // tooltips

  /* Tooltip container */
  .tooltips {
    width: 14px;
    height: 14px;
    margin-left: 5px;
    transform: translateY(-1px);
    display: inline-block;
    cursor: pointer;
  }

  /* Tooltip text */
  .tooltips .tooltiptext {
    visibility: hidden;
    width: 220px;
    background-color: #ffffff;
    color: var(--dark--color);
    text-align: center;
    padding: 15px 10px;
    filter: drop-shadow(0px 5px 20px rgba(0, 0, 0, 0.4));
    @media (max-width: 576px) {
      width: 180px;
      font-size: 14px;
      padding: 10px;
    }

    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 0%;
    margin-left: -140px;


    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }

  /* Tooltip arrow */
  .tooltips .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 148px;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
    @media (max-width: 576px) {
      left: 148px;
    }
  }

  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltips:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }
